/**
 * External dependencies.
 */
import { __ } from '@wordpress/i18n';
import { Fragment, useContext, useEffect } from '@wordpress/element';
import { Placeholder, PanelBody, SelectControl, ToggleControl, Spinner, Toolbar } from '@wordpress/components';
import { InspectorControls, BlockControls } from '@wordpress/editor';
import { PropTypes } from 'prop-types';

/**
 * Internal dependencies.
 */
import BookingContainer from '../../components/event-table/container';
import ProductsScreen from '../../components/products/screen';
import CategoriesScreen from '../../components/categories/screen';
import ResourcesScreen from '../../components/resources/screen';
import { IconListAlt } from '../../components/icons';
import { DATE_RANGE_OPTIONS, DATE_RANGES } from '../../includes/date-utils';
import { screenOptions, SCREEN } from '../../includes/misc-utils';
import InitialProductsScreen from '../../components/initial-products-screen';
import InfiniteScrollControl from '../../components/infinite-scroll-control';
import commonContext from '../common-context/context';
import productsContext from '../products-context/context';
import scheduleContext from './context/context';
import productSelectionContext from '../../components/product-selection-context/context';

const Block = ( props ) => {
	const {
		onShowDateNavUpdate,
		showDateNav,
		showEmptyDates,
		onShowEmptyDatesUpdate,
	} = useContext( commonContext );

	const {
		onScreenUpdate,
		onEdit,
	} = useContext( productSelectionContext );

	const {
		requestProducts,
		getProducts,
		productsRequesting,
	} = useContext( productsContext );

	const {
		dateRangeType,
	} = useContext( scheduleContext );

	useEffect( () => {
		if ( ! productsRequesting && getProducts().count === false ) {
			requestProducts();
		}
	}, [] );

	const onInfiniteScrollUpdate = ( infScrollEn, slots ) => {
		const { setAttributes } = props;

		setAttributes( {
			infiniteScrollEnabled: infScrollEn,
			slotsPerPage: slots,
		} );
	};

	const onDateRangeUpdate = ( dateRangeId ) => {
		const { setAttributes } = props;
		const newAttributes = { dateRangeType: dateRangeId };
		// Implement smart defaults that change infinite scroll based on date range type
		if ( dateRangeId !== dateRangeType ) {
			switch ( dateRangeId ) {
				case DATE_RANGES.TODAY:
					newAttributes.infiniteScrollEnabled = true;
					break;
				case DATE_RANGES.THIS_WEEK:
				case DATE_RANGES.THIS_MONTH:
					newAttributes.infiniteScrollEnabled = false;
					break;
			}
		}

		setAttributes( newAttributes );
	};

	const getInspectorControls = () => {
		const {
			currentScreen,
			infiniteScrollEnabled,
			slotsPerPage,
		} = props.attributes;

		return (
			<InspectorControls key="inspector">
				<PanelBody
					title={ __( 'Schedule settings', 'woocommerce-bookings-availability' ) }
					initialOpen={ false }
				>
					<SelectControl
						label={ __( 'Include', 'woocommerce-bookings-availability' ) }
						value={ currentScreen }
						options={ screenOptions }
						onChange={ onScreenUpdate }
					/>
				</PanelBody>
				<PanelBody
					title={ __( 'Display settings', 'woocommerce-bookings-availability' ) }
					initialOpen={ false }
				>
					<SelectControl
						label={ __( 'Time range', 'woocommerce-bookings-availability' ) }
						value={ dateRangeType }
						options={ DATE_RANGE_OPTIONS }
						onChange={ onDateRangeUpdate }
					/>

					<ToggleControl
						label={ __( 'Show date navigation', 'woocommerce-bookings-availability' ) }
						checked={ showDateNav }
						onChange={ onShowDateNavUpdate }
					/>

					<InfiniteScrollControl
						infiniteScrollEnabled={ infiniteScrollEnabled }
						slotsPerPage={ slotsPerPage }
						onChange={ onInfiniteScrollUpdate }
					/>

					<ToggleControl
						label={ __( 'Show empty dates', 'woocommerce-bookings-availability' ) }
						checked={ showEmptyDates }
						onChange={ onShowEmptyDatesUpdate }
					/>
				</PanelBody>
			</InspectorControls>
		);
	};

	const renderScreen = () => {
		const { attributes } = props;
		const {
			currentScreen,
			infiniteScrollEnabled,
			slotsPerPage,
		} = attributes;

		switch ( currentScreen ) {
			case SCREEN.ALL_PRODUCTS:
				return (
					<Fragment>
						<BlockControls>
							<Toolbar
								controls={ [
									{
										icon: 'edit',
										title: __( 'Edit', 'woocommerce-bookings-availability' ),
										onClick: onEdit,
									},
								] }
							/>
						</BlockControls>
						<div>
							<BookingContainer
								isPreview
								dateRangeType={ dateRangeType }
								infiniteScrollEnabled={ infiniteScrollEnabled }
								slotsPerPage={ slotsPerPage }
							/>
						</div>
					</Fragment>
				);
			case SCREEN.PRODUCTS_SCREEN:
				return <ProductsScreen
					dateRangeType={ dateRangeType }
					blockType={ 'schedule' }
					infiniteScrollEnabled={ infiniteScrollEnabled }
					slotsPerPage={ slotsPerPage }
				/>;
			case SCREEN.CATEGORIES_SCREEN:
				return <CategoriesScreen
					dateRangeType={ dateRangeType }
					blockType={ 'schedule' }
					infiniteScrollEnabled={ infiniteScrollEnabled }
					slotsPerPage={ slotsPerPage }
				/>;
			case SCREEN.RESOURCES_SCREEN:
				return <ResourcesScreen
					dateRangeType={ dateRangeType }
					blockType={ 'schedule' }
					infiniteScrollEnabled={ infiniteScrollEnabled }
					slotsPerPage={ slotsPerPage }
				/>;
			default:
				return <InitialProductsScreen blockType={ 'schedule' } />;
		}
	};

	const productsCount = getProducts().count;

	if ( productsRequesting || false === productsCount || 0 === productsCount ) {
		return (
			<Placeholder
				icon={ <IconListAlt /> }
				label={ __( 'Bookings Schedule', 'woocommerce-bookings-availability' ) }>
				{ productsRequesting ? (
					<Spinner />
				) : (
					<div>
						{ __( 'You haven\'t created any bookable products.', 'woocommerce-bookings-availability' ) }
						{ ' ' }
						<a href="post-new.php?post_type=product&amp;bookable_product=1">{ __( 'Create one now.', 'woocommerce-bookings-availability' ) }</a>
					</div>
				) }
			</Placeholder>
		);
	}

	return (
		<Fragment>
			{ renderScreen() }
			{ getInspectorControls() }
		</Fragment>
	);
};

Block.propTypes = {
	/**
	 * The attributes for this block
	 */
	attributes: PropTypes.object.isRequired,
	/**
	 * A callback to update attributes
	 */
	setAttributes: PropTypes.func.isRequired,
};

export default Block;
