/**
 * External dependencies
 */
import { useState } from '@wordpress/element';
import { PropTypes } from 'prop-types';
import { ToggleControl, TextControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import { DEFAULT_PAGE_SIZE } from '../../includes/misc-utils';
import './style.scss';

const MINIMUM_SLOTS_PER_PAGE = 1;

const InfiniteScrollControl = ( props ) => {
	const [ currentSlotsPerPage, setCurrentSlotsPerPage ] = useState( props.slotsPerPage );
	const { infiniteScrollEnabled, slotsPerPage } = props;

	/*
		* Validate slots per page value and use default for non-numbers.
		*/
	const validateSlotsPerPage = ( _slotsPerPage ) => {
		const parsedSlots = parseInt( _slotsPerPage, 10 ) || DEFAULT_PAGE_SIZE;

		return Math.max( MINIMUM_SLOTS_PER_PAGE, parsedSlots );
	};

	/*
		* Pass updated (and validated) values to parent's onChange handler.
		*/
	const handleChange = ( _infiniteScrollEnabled, _slotsPerPage ) => {
		const { onChange } = props;

		onChange( _infiniteScrollEnabled, validateSlotsPerPage( _slotsPerPage ) );
	};

	const onBlur = ( e ) => {
		setCurrentSlotsPerPage( validateSlotsPerPage( e.target.value ) );
	};

	return (
		<div className="wc-bookings-availability-infinite-scroll-control">
			<ToggleControl
				label={ __( 'Show all available times', 'woocommerce-bookings-availability' ) }
				checked={ infiniteScrollEnabled }
				onChange={ ( updateEnabled ) => handleChange( updateEnabled, slotsPerPage ) }
			/>

			{ ! infiniteScrollEnabled && (
				<TextControl
					label={ __( 'Maximum number of times to show at first', 'woocommerce-bookings-availability' ) }
					type="number"
					min={ MINIMUM_SLOTS_PER_PAGE }
					value={ currentSlotsPerPage }
					onChange={ ( value ) => {
						setCurrentSlotsPerPage( value );
						handleChange( infiniteScrollEnabled, value );
					} }
					onBlur={ onBlur }
				/>
			) }
		</div>
	);
};

InfiniteScrollControl.propTypes = {
	/**
	 * Whether infinite scroll is enabled.
	 */
	infiniteScrollEnabled: PropTypes.bool.isRequired,
	/**
	 * The maximum number of slots to show for non-infinite scroll.
	 */
	slotsPerPage: PropTypes.number.isRequired,

	/**
	 * Change handler.
	 */
	onChange: PropTypes.func,
};

export default InfiniteScrollControl;
