/**
 * External dependencies.
 */
import { __ } from '@wordpress/i18n';
import { Placeholder, MenuGroup, MenuItem } from '@wordpress/components';
import { useContext } from '@wordpress/element';
import { PropTypes } from 'prop-types';

/**
 * Internal dependencies.
 */
import { IconListAlt, IconCalendarAlt, IconKeyboardArrowRight } from '../../components/icons';
import { screenOptions } from '../../includes/misc-utils';
import productSelectionContext from '../product-selection-context/context';
import './style.scss';

const InitialProductsScreen = ( props ) => {
	const { onSelectionScreenUpdate } = useContext( productSelectionContext );
	const { blockType } = props;

	let placeholderIcon = '';
	let placeholderLabel = '';

	switch ( blockType ) {
		case 'calendar':
			placeholderIcon = <IconCalendarAlt />;
			placeholderLabel = __( 'Bookings Calendar', 'woocommerce-bookings-availability' );
			break;
		case 'schedule':
			placeholderIcon = <IconListAlt />;
			placeholderLabel = __( 'Bookings Schedule', 'woocommerce-bookings-availability' );
			break;
	}

	return (
		<Placeholder
			icon={ placeholderIcon }
			label={ placeholderLabel }
			className="wc-bookings-availability-screen">
			<div className="woocommerce-bookings-availability-desc">
				{ __(
					'Display a schedule of upcoming events or classes',
					'woocommerce-bookings-availability'
				) }
			</div>
			<hr />
			<div className="wc-bookings-availability-bookable-products-__selection">
				<MenuGroup label={ __( 'Select bookable products to display', 'woocommerce-bookings-availability' ) }>
					{ screenOptions.map( ( data, rowIndex ) => (
						<MenuItem key={ rowIndex } onClick={ onSelectionScreenUpdate( data.value ) }>{ data.label }
							<IconKeyboardArrowRight />
						</MenuItem>
					) ) }
				</MenuGroup>
			</div>
		</Placeholder>
	);
};

InitialProductsScreen.propTypes = {
	blockType: PropTypes.string,
};

export default InitialProductsScreen;
