/**
 * External dependencies
 */
import { __ } from '@wordpress/i18n';
import { BlockControls } from '@wordpress/editor';
import {
	Button,
	Placeholder,
	Toolbar,
} from '@wordpress/components';
import { useContext, Fragment } from '@wordpress/element';
import PropTypes from 'prop-types';

/**
 * Internal dependencies
 */
import { IconListAlt, IconCalendarAlt } from '../../icons';
import ResourcesControl from '../control';
import BookingContainer from '../../event-table/container';
import CalendarContainer from '../../event-calendar/container';
import productSelectionContext from '../../product-selection-context/context';

/**
 * Component to handle edit mode of "Specific Resources".
 */
const ResourcesScreen = ( props ) => {
	const {
		triggerSetEditModeResources,
		triggerSetSelectedResources,
		selectedResources,
		editModeResources,
		onDoneResources,
		onGoBack,
	} = useContext( productSelectionContext );

	const placeholderIcon = () => {
		const { blockType } = props;

		switch ( blockType ) {
			case 'calendar':
				return <IconCalendarAlt />;
			case 'schedule':
				return <IconListAlt />;
		}
	};

	const onClickDone = () => {
		triggerSetEditModeResources( false );
		onDoneResources( selectedResources );
	};

	const onEdit = () => {
		triggerSetEditModeResources( ! editModeResources );
	};

	const onChange = ( value = [] ) => {
		const ids = value.map( ( { id } ) => id );
		triggerSetSelectedResources( ids );
	};

	const renderEditMode = () => {
		return (
			<Placeholder
				icon={ placeholderIcon() }
				label={ __( 'Specific bookable resources', 'woocommerce-bookings-availability' ) }
				className="wc-bookings-availability-screen wc-bookings-availability-specific-bookable-resources"
			>
				<div>
					<div>
						{ __(
							'Display a selection of specific bookable resources in a grid',
							'woocommerce-bookings-availability'
						) }
					</div>
					<a href="#" className="wc-bookings-availability-back-to-initial-screen-link" onClick={ onGoBack }>{ /* eslint-disable-line jsx-a11y/anchor-is-valid */ }
						{ __( 'Display different products', 'woocommerce-bookings-availability' ) }
					</a>
				</div>
				<div className="wc-bookings-availability-specific-bookable-resources__selection">
					<ResourcesControl
						selected={ selectedResources }
						onChange={ onChange }
					/>
					<Button isDefault onClick={ onClickDone }>
						{ __( 'Done', 'woocommerce-bookings-availability' ) }
					</Button>
				</div>
			</Placeholder>
		);
	};

	const hasSelectedResources = selectedResources && selectedResources.length;
	const { attributes } = props;
	const { dateRangeType, blockType, infiniteScrollEnabled, slotsPerPage } = props;
	const classes = [ 'wc-bookings-availability-specific-bookable-resources-grid', 'wc-bookings-availability-specific-bookable-resources' ];

	if ( ! hasSelectedResources ) {
		classes.push( 'is-not-found' );
	}

	return (
		<Fragment>
			<BlockControls>
				<Toolbar
					controls={ [
						{
							icon: 'edit',
							title: __( 'Edit' ),
							onClick: onEdit,
							isActive: editModeResources,
						},
					] }
				/>
			</BlockControls>
			{ editModeResources ? (
				renderEditMode()
			) : (
				<div className={ classes.join( ' ' ) }>
					{ hasSelectedResources ? (
						<Fragment>
							{ 'calendar' === blockType ? (
								<CalendarContainer isPreview resourceIds={ selectedResources.join( ',' ) } { ...attributes } />
							) : (
								<BookingContainer
									isPreview
									dateRangeType={ dateRangeType }
									resourceIds={ selectedResources.join( ',' ) }
									infiniteScrollEnabled={ infiniteScrollEnabled }
									slotsPerPage={ slotsPerPage }
								/>
							) }
						</Fragment>
					) : (
						<Placeholder
							icon={ placeholderIcon() }
							label={ __(
								'Specific Bookable Resources',
								'woocommerce-bookings-availability'
							) }
						>
							{ __(
								'No resources are selected.',
								'woocommerce-bookings-availability'
							) }
						</Placeholder>
					) }
				</div>
			) }
		</Fragment>
	);
};

ResourcesScreen.propTypes = {
	/**
	 * Callback to update the selected resources.
	 */
	header: PropTypes.object,

	blockType: PropTypes.string,

	dateRangeType: PropTypes.string,

	infiniteScrollEnabled: PropTypes.bool,

	slotsPerPage: PropTypes.number,

	attributes: PropTypes.object,
};

export default ResourcesScreen;
