/**
 * External dependencies
 */
import PropTypes from 'prop-types';

/**
 * External dependencies.
 */
import { useState } from '@wordpress/element';

/**
 * Internal dependencies.
 */
import ProductSelectionContext from './context';

const ProductSelectionState = ( props ) => {
	/**
	 * onSelectionScreenUpdate. In the editor.
	 */
	const onSelectionScreenUpdate = ( screenId ) => () => {
		props.setAttributes( {
			currentScreen: screenId,
			productIds: '',
			categoryIds: '',
			resourceIds: '',
		} );
	};

	/**
	 * onScreenUpdate. In the sidebar settings.
	 */
	const onScreenUpdate = ( screenId ) => {
		props.setAttributes( {
			currentScreen: screenId,
			productIds: '',
			categoryIds: '',
			resourceIds: '',
		} );
	};

	/**
	 * onGoBack.
	 */
	const onGoBack = ( screenId ) => {
		props.setAttributes( {
			currentScreen: screenId,
			productIds: '',
			categoryIds: '',
			resourceIds: '',
		} );
	};

	/**
	 * onEdit.
	 */
	const onEdit = () => {
		props.setAttributes( { currentScreen: '' } );
	};

	/**
	 * editModeProducts.
	 */
	const [ editModeProducts, setEditModeProducts ] = useState( props.attributes.productIds.length ? false : true );

	const triggerSetEditModeProducts = ( value ) => {
		setEditModeProducts( value );
	};

	/**
	 * selectedProducts.
	 */
	const [ selectedProducts, setSelectedProducts ] = useState( props.attributes.productIds.length ? props.attributes.productIds.split( ',' ) : [] );

	const triggerSetSelectedProducts = ( value ) => {
		setSelectedProducts( value );
	};

	/**
	 * onDoneProducts.
	 */
	const onDoneProducts = ( ids ) => {
		props.setAttributes( { productIds: ids.join( ',' ) } );
	};

	/**
	 * editModeCategories.
	 */
	const [ editModeCategories, setEditModeCategories ] = useState( props.attributes.categoryIds.length ? false : true );

	const triggerSetEditModeCategories = ( value ) => {
		setEditModeCategories( value );
	};

	/**
	 * selectedCategories.
	 */
	const [ selectedCategories, setSelectedCategories ] = useState( props.attributes.categoryIds.length ? props.attributes.categoryIds.split( ',' ) : [] );

	const triggerSetSelectedCategories = ( value ) => {
		setSelectedCategories( value );
	};

	/**
	 * onDoneCategories.
	 */
	const onDoneCategories = ( ids ) => {
		props.setAttributes( { categoryIds: ids.join( ',' ) } );
	};

	/**
	 * editModeResources.
	 */
	const [ editModeResources, setEditModeResources ] = useState( props.attributes.resourceIds.length ? false : true );

	const triggerSetEditModeResources = ( value ) => {
		setEditModeResources( value );
	};

	/**
	 * selectedResources.
	 */
	const [ selectedResources, setSelectedResources ] = useState( props.attributes.resourceIds.length ? props.attributes.resourceIds.split( ',' ) : [] );

	const triggerSetSelectedResources = ( value ) => {
		setSelectedResources( value );
	};

	/**
	 * onDoneResources.
	 */
	const onDoneResources = ( ids ) => {
		props.setAttributes( { resourceIds: ids.join( ',' ) } );
	};

	return (
		<ProductSelectionContext.Provider value={ {
			onScreenUpdate: onScreenUpdate,
			onSelectionScreenUpdate: onSelectionScreenUpdate,
			onGoBack: onGoBack,
			triggerSetEditModeProducts: triggerSetEditModeProducts,
			editModeProducts: editModeProducts,
			triggerSetEditModeCategories: triggerSetEditModeCategories,
			editModeCategories: editModeCategories,
			triggerSetEditModeResources: triggerSetEditModeResources,
			editModeResources: editModeResources,
			onEdit: onEdit,
			selectedProducts: selectedProducts,
			triggerSetSelectedProducts: triggerSetSelectedProducts,
			onDoneProducts: onDoneProducts,
			selectedCategories: selectedCategories,
			triggerSetSelectedCategories: triggerSetSelectedCategories,
			onDoneCategories: onDoneCategories,
			selectedResources: selectedResources,
			triggerSetSelectedResources: triggerSetSelectedResources,
			onDoneResources: onDoneResources,
		} }
		>
			{ props.children }
		</ProductSelectionContext.Provider>
	);
};

ProductSelectionState.propTypes = {
	children: PropTypes.object,
	attributes: PropTypes.object,
	setAttributes: PropTypes.func,
	productIds: PropTypes.node,
};

export default ProductSelectionState;
