/**
 * External dependencies
 */
import { __, _n, sprintf } from '@wordpress/i18n';
import apiFetch from '@wordpress/api-fetch';
import { Fragment, useState, useEffect } from '@wordpress/element';
import { find } from 'lodash';
import PropTypes from 'prop-types';
import { addQueryArgs } from '@wordpress/url';

/**
 * Internal dependencies
 */
import SearchListControl from '../../search-list-control';

const ProductsControl = ( props ) => {
	const [ list, setList ] = useState( [] );
	const [ loading, setLoading ] = useState( true );

	useEffect( () => {
		apiFetch( {
			path: addQueryArgs( '/wc-bookings/v1/products', { per_page: -1 } ),
		} )
			.then( ( _list ) => {
				_list = _list.map( ( item ) => ( { id: item.id, name: item.name } ) );
				setList( _list );
				setLoading( false );
			} )
			.catch( () => {
				setList( [] );
				setLoading( false );
			} );
	}, [] );

	const { onChange, selected } = props;

	const messages = {
		clear: __( 'Clear all products', 'woocommerce-bookings-availability' ),
		list: __( 'Products', 'woocommerce-bookings-availability' ),
		noItems: __(
			"Your store doesn't have any products.",
			'woocommerce-bookings-availability'
		),
		search: __(
			'Search for products to display',
			'woocommerce-bookings-availability'
		),
		selected: ( n ) =>
			/* translators: %d name of the product. */
			sprintf(
				_n(
					'%d product selected',
					'%d products selected',
					n,
					'woocommerce-bookings-availability'
				),
				n
			),
		updated: __(
			'Product search results updated.',
			'woocommerce-bookings-availability'
		),
	};

	return (
		<Fragment>
			<SearchListControl
				className="woocommerce-products"
				list={ list }
				isLoading={ loading }
				selected={ selected.map( ( id ) => parseInt( id, 10 ) ).map( ( id ) => find( list, { id } ) ).filter( Boolean ) }
				onChange={ onChange }
				messages={ messages }
			/>
		</Fragment>
	);
};

ProductsControl.propTypes = {
	/**
	 * Callback to update the selected products.
	 */
	onChange: PropTypes.func.isRequired,
	/**
	 * The list of currently selected IDs.
	 */
	selected: PropTypes.array.isRequired,
};

export default ProductsControl;
