/**
 * External dependencies
 */
import { __ } from '@wordpress/i18n';
import { BlockControls } from '@wordpress/editor';
import {
	Button,
	Placeholder,
	Toolbar,
} from '@wordpress/components';
import { useContext, Fragment } from '@wordpress/element';
import PropTypes from 'prop-types';

/**
 * Internal dependencies
 */
import { IconListAlt, IconCalendarAlt } from '../../icons';
import CategoriesControl from '../control';
import BookingContainer from '../../event-table/container';
import CalendarContainer from '../../event-calendar/container';
import productSelectionContext from '../../product-selection-context/context';

/**
 * Component to handle edit mode of "Specific Categories".
 */
const CategoriesScreen = ( props ) => {
	const {
		triggerSetEditModeCategories,
		triggerSetSelectedCategories,
		editModeCategories,
		onDoneCategories,
		selectedCategories,
		onGoBack,
	} = useContext( productSelectionContext );

	const placeholderIcon = () => {
		const { blockType } = props;

		switch ( blockType ) {
			case 'calendar':
				return <IconCalendarAlt />;
			case 'schedule':
				return <IconListAlt />;
		}
	};

	const onClickDone = () => {
		triggerSetEditModeCategories( false );
		onDoneCategories( selectedCategories );
	};

	const onEdit = () => {
		triggerSetEditModeCategories( ! editModeCategories );
	};

	const onChange = ( value = [] ) => {
		const ids = value.map( ( { id } ) => id );
		triggerSetSelectedCategories( ids );
	};

	const renderEditMode = () => {
		return (
			<Placeholder
				icon={ placeholderIcon() }
				label={ __( 'Specific bookable categories', 'woocommerce-bookings-availability' ) }
				className="wc-bookings-availability-screen wc-bookings-availability-specific-bookable-categories"
			>
				<div>
					<div>
						{ __(
							'Display a selection of specific bookable categories in a grid',
							'woocommerce-bookings-availability'
						) }
					</div>
					<a href="#" className="wc-bookings-availability-back-to-initial-screen-link" onClick={ onGoBack }>{ /* eslint-disable-line jsx-a11y/anchor-is-valid */ }
						{ __( 'Display different products', 'woocommerce-bookings-availability' ) }
					</a>
				</div>
				<div className="wc-bookings-availability-specific-bookable-categories__selection">
					<CategoriesControl
						selected={ selectedCategories }
						onChange={ onChange }
					/>
					<Button isDefault onClick={ onClickDone }>
						{ __( 'Done', 'woocommerce-bookings-availability' ) }
					</Button>
				</div>
			</Placeholder>
		);
	};

	const hasSelectedCategories = selectedCategories && selectedCategories.length;
	const { attributes } = props;
	const { dateRangeType, blockType, infiniteScrollEnabled, slotsPerPage } = props;
	const classes = [ 'wc-bookings-availability-specific-bookable-categories-grid', 'wc-bookings-availability-specific-bookable-categories' ];

	if ( ! hasSelectedCategories ) {
		classes.push( 'is-not-found' );
	}

	return (
		<Fragment>
			<BlockControls>
				<Toolbar
					controls={ [
						{
							icon: 'edit',
							title: __( 'Edit' ),
							onClick: onEdit,
							isActive: editModeCategories,
						},
					] }
				/>
			</BlockControls>
			{ editModeCategories ? (
				renderEditMode()
			) : (
				<div className={ classes.join( ' ' ) }>
					{ hasSelectedCategories ? (
						<Fragment>
							{ 'calendar' === blockType ? (
								<CalendarContainer isPreview categoryIds={ selectedCategories.join( ',' ) } { ...attributes } />
							) : (
								<BookingContainer
									isPreview
									dateRangeType={ dateRangeType }
									categoryIds={ selectedCategories.join( ',' ) }
									infiniteScrollEnabled={ infiniteScrollEnabled }
									slotsPerPage={ slotsPerPage }
								/>
							) }
						</Fragment>
					) : (
						<Placeholder
							icon={ placeholderIcon() }
							label={ __(
								'Specific Bookable Categories',
								'woocommerce-bookings-availability'
							) }
						>
							{
								__(
									'No categories are selected.',
									'woocommerce-bookings-availability'
								)
							}
						</Placeholder>
					) }
				</div>
			) }
		</Fragment>
	);
};

CategoriesScreen.propTypes = {
	/**
	 * Callback to update the selected categories.
	 */
	categoryIds: PropTypes.array,

	header: PropTypes.object,

	blockType: PropTypes.string,

	dateRangeType: PropTypes.string,

	infiniteScrollEnabled: PropTypes.bool,

	slotsPerPage: PropTypes.number,

	attributes: PropTypes.object,
};

export default CategoriesScreen;
