/**
 * External dependencies
 */
import { __ } from '@wordpress/i18n';
import { kebabCase, isObject } from 'lodash';

const { registerBlockType } = wp.blocks;

/**
 * Internal dependencies
 */
import Block from './block';
import { IconListAlt } from '../../components/icons';
import { getAttributesDefFor } from '../../includes/misc-utils';
import { saveDeprecatedBlockSchedule, getDeprecatedAttributesDefFor } from '../../includes/deprecation';
import './style.scss';
import CommonState from '../common-context/common-state';
import ProductSelectionState from '../../components/product-selection-context/product-selection-state';
import ProductsState from '../products-context/products-state';
import ScheduleState from './context/schedule-state';

// Get attribute defintions for schedule block.
const attributesDef = getAttributesDefFor( 'schedule' );

registerBlockType( 'woocommerce/bookings-availability-schedule-block', {
	title: __( 'Bookings Schedule', 'woocommerce-bookings-availability' ),
	description: __( 'Show customers your upcoming availability in list form.', 'woocommerce-bookings-availability' ),
	icon: <IconListAlt />,
	category: 'woocommerce',
	keywords: [ 'events', 'woocommerce', 'bookings' ],
	supports: {
		align: [ 'wide', 'full' ],
	},
	example: {
		attributes: {
			currentScreen: 'all-products',
		},
	},
	attributes: attributesDef,
	deprecated: [
		{
			attributes: getDeprecatedAttributesDefFor( '1.0.0', 'schedule' ),
			save: ( props ) => saveDeprecatedBlockSchedule( '1.0.0', props ),
		},
	],
	edit( props ) {
		return (
			<CommonState { ...props }>
				<ProductSelectionState { ...props }>
					<ProductsState { ...props }>
						<ScheduleState { ...props }>
							<Block { ...props } />
						</ScheduleState>
					</ProductsState>
				</ProductSelectionState>
			</CommonState>
		);
	},
	save( props ) {
		const { attributes } = props;
		const dataProps = {};

		// Build object with 'data-' property names.
		for ( const propName in attributes ) {
			const attributeDef = attributesDef[ propName ];

			if ( ! isObject( attributeDef ) || ! attributeDef.attribute || ! attributeDef.attribute.startsWith( 'data-' ) ) {
				continue;
			}

			const dataPropName = 'data-' + kebabCase( propName );
			dataProps[ dataPropName ] = attributes[ propName ];
		}

		return (
			<div className="wc-bookings-availability-main-events-container wc-bookings-availability-main-container" { ...dataProps }>
			</div>
		);
	},
} );
