/**
 * External dependencies
 */
import { kebabCase, isObject } from 'lodash';

/**
 * Internal dependencies
 */
import { DATE_RANGES } from './date-utils';
import { DEFAULT_PAGE_SIZE } from './misc-utils';

/*
 * Global list of all attributes for both blocks.
 * Attributes are organized into 'common', 'calendar',
 * and 'schedule' props. 'common' attributes
 * apply to both blocks while 'calendar' and 'schedule'
 * apply to the specific block types only.
 *
 *
 * MODIFYING THIS MAY CAUSE BLOCK VALIDATION
 * ERRORS FOR ANY BLOCKS SAVED WITH EARLIER
 * VERSIONS.
 *
 */
const deprecatedAttributesDictionary = {
	// This is a copy of the 1.0.0 attribute list.
	'1.0.0': {
		common: {
			categoryIds: {
				type: 'string',
				default: '',
				source: 'attribute',
				selector: '.wc-bookings-availability-main-container',
				attribute: 'data-category-ids',
			},
			currentScreen: {
				type: 'string',
				default: '',
			},
			productIds: {
				type: 'string',
				default: '',
				source: 'attribute',
				selector: '.wc-bookings-availability-main-container',
				attribute: 'data-product-ids',
			},
			resourceIds: {
				type: 'string',
				default: '',
				source: 'attribute',
				selector: '.wc-bookings-availability-main-container',
				attribute: 'data-resource-ids',
			},
			showDateNav: {
				type: 'boolean',
				default: true,
				attribute: 'data-show-date-nav',
			},
		},
		calendar: {
			dateRangeType: {
				type: 'string',
				default: DATE_RANGES.THIS_MONTH,
				attribute: 'data-date-range-type',
			},
			showSoldOut: {
				type: 'boolean',
				default: true,
				attribute: 'data-show-sold-out',
			},
		},
		schedule: {
			dateRangeType: {
				type: 'string',
				default: DATE_RANGES.TODAY,
				attribute: 'data-date-range-type',
			},
			infiniteScrollEnabled: {
				type: 'boolean',
				default: true,
				attribute: 'data-infinite-scroll-enabled',
			},
			maxDate: {
				type: 'date',
				default: null,
				source: 'attribute',
				selector: '.wc-bookings-availability-container',
				attribute: 'data-max-date',
			},
			minDate: {
				type: 'date',
				default: null,
				source: 'attribute',
				selector: '.wc-bookings-availability-container',
				attribute: 'data-min-date',
			},
			slotsPerPage: {
				type: 'number',
				default: DEFAULT_PAGE_SIZE,
				attribute: 'data-slots-per-page',
			},
		},
	},
};

/**
 * Get attribute definitions for the given block type name.
 *
 * @param {string} blockName Name of block to retrieve from (e.g. 'calendar' or 'schedule').
 * @param {string} version   Version string for when attributes were valid (e.g. '1.0.0').
 *
 * @return {object} Attribute defintions for block.
 */
export function getDeprecatedAttributesDefFor( version, blockName ) {
	// Merge common attributes with the specific block attributes.
	return { ...deprecatedAttributesDictionary[ version ].common, ...deprecatedAttributesDictionary[ version ][ blockName ] };
}

/**
 * Get block content for calendar block to be saved for deprecated save function.
 *
 * @param {string} version Version string to query attributes for (e.g. '1.0.0').
 * @param {object} props Block attribute values.
 *
 * @return {object} Element to be saved as block content.
 */
export function saveDeprecatedBlockCalendar( version, props ) {
	return saveDeprecatedBlock( 'woocommerce-bookings-availability-calendar-container', version, 'calendar', props );
}

/**
 * Get block content for schedule block to be saved for deprecated save function.
 *
 * @param {string} version Version string to query attributes for (e.g. '1.0.0').
 * @param {object} props   Block attribute values.
 *
 * @return {object} Element to be saved as block content.
 */
export function saveDeprecatedBlockSchedule( version, props ) {
	return saveDeprecatedBlock( 'wc-bookings-availability-main-events-container wc-bookings-availability-main-container', version, 'schedule', props );
}

/**
 * Get block content for block to be saved for deprecated save function.
 * Returned content should match content for 1.0.0 release.
 *
 * @param {string} blockClassName Class to be used for container element.
 * @param {string} version        Version string to query attributes for (e.g. '1.0.0').
 * @param {string} blockName      Block to retrieve attributes for ('calendar' or 'schedule').
 * @param {object} props          Block attribute values.
 *
 * @return {object} Element to be saved as block content.
 */
function saveDeprecatedBlock( blockClassName, version, blockName, props ) {
	const { attributes } = props; /* eslint-disable-line react/prop-types */
	const attributesDef = getDeprecatedAttributesDefFor( version, blockName );
	const dataProps = {};
	// Build object with 'data-' property names.
	for ( const propName in attributes ) {
		const attributeDef = attributesDef[ propName ];

		if ( ! isObject( attributeDef ) || ! attributeDef.attribute || ! attributeDef.attribute.startsWith( 'data-' ) ) {
			continue;
		}

		const dataPropName = 'data-' + kebabCase( propName );
		dataProps[ dataPropName ] = attributes[ propName ];
	}

	return (
		<div className={ blockClassName } { ...dataProps } >
		</div>
	);
}
