/**
 * External dependencies
 */
import { __ } from '@wordpress/i18n';
import { BlockControls } from '@wordpress/editor';
import {
	Button,
	Placeholder,
	Toolbar,
} from '@wordpress/components';
import { useContext, Fragment } from '@wordpress/element';
import PropTypes from 'prop-types';

/**
 * Internal dependencies
 */
import { IconListAlt, IconCalendarAlt } from '../../icons';
import ProductsControl from '../control';
import BookingContainer from '../../event-table/container';
import CalendarContainer from '../../event-calendar/container';
import productSelectionContext from '../../product-selection-context/context';

/**
 * Component to handle edit mode of "Specific Products".
 */
const ProductsScreen = ( props ) => {
	const {
		selectedProducts,
		editModeProducts,
		triggerSetEditModeProducts,
		onDoneProducts,
		onGoBack,
		triggerSetSelectedProducts } = useContext( productSelectionContext );

	const placeholderIcon = () => {
		const { blockType } = props;

		switch ( blockType ) {
			case 'calendar':
				return <IconCalendarAlt />;
			case 'schedule':
				return <IconListAlt />;
		}
	};

	const onClickDone = () => {
		triggerSetEditModeProducts( false );
		onDoneProducts( selectedProducts );
	};

	const onEdit = () => {
		triggerSetEditModeProducts( ! editModeProducts );
	};

	const onChange = ( value = [] ) => {
		const ids = value.map( ( { id } ) => id );
		triggerSetSelectedProducts( ids );
	};

	const renderEditMode = () => {
		return (
			<Placeholder
				icon={ placeholderIcon() }
				label={ __( 'Specific bookable products', 'woocommerce-bookings-availability' ) }
				className="wc-bookings-availability-screen wc-block-specific-bookable-products"
			>
				<div>
					<div>
						{ __(
							'Display a selection of specific bookable products in a list',
							'woocommerce-bookings-availability'
						) }
					</div>
					<a href="#" className="wc-bookings-availability-back-to-initial-screen-link" onClick={ onGoBack }>{ /* eslint-disable-line jsx-a11y/anchor-is-valid */ }
						{ __( 'Display different products', 'woocommerce-bookings-availability' ) }
					</a>
				</div>
				<div className="wc-bookings-availability-specific-bookable-products__selection">
					<ProductsControl
						selected={ selectedProducts }
						onChange={ onChange }
					/>
					<Button isDefault onClick={ onClickDone }>
						{ __( 'Done', 'woocommerce-bookings-availability' ) }
					</Button>
				</div>
			</Placeholder>
		);
	};

	const hasSelectedProducts = selectedProducts && selectedProducts.length;
	const { attributes } = props;
	const { blockType, dateRangeType, infiniteScrollEnabled, slotsPerPage } = props;
	const classes = [ 'wc-bookings-availability-specific-bookable-products-grid', 'wc-block-specific-bookable-products' ];

	if ( ! hasSelectedProducts ) {
		classes.push( 'is-not-found' );
	}

	return (
		<Fragment>
			<BlockControls>
				<Toolbar
					controls={ [
						{
							icon: 'edit',
							title: __( 'Edit', 'woocommerce-bookings-availability' ),
							onClick: onEdit,
							isActive: editModeProducts,
						},
					] }
				/>
			</BlockControls>
			{ editModeProducts ? (
				renderEditMode()
			) : (
				<div className={ classes.join( ' ' ) }>
					{ hasSelectedProducts ? (
						<Fragment>
							{ 'calendar' === blockType ? (
								<CalendarContainer isPreview productIds={ selectedProducts.join( ',' ) } { ...attributes } />
							) : (
								<BookingContainer
									isPreview
									dateRangeType={ dateRangeType }
									productIds={ selectedProducts.join( ',' ) }
									infiniteScrollEnabled={ infiniteScrollEnabled }
									slotsPerPage={ slotsPerPage }
								/>
							) }
						</Fragment>
					) : (
						<Placeholder
							icon={ placeholderIcon() }
							label={ __(
								'Specific Bookable Products',
								'woocommerce-bookings-availability'
							) }
						>
							{
								__(
									'No products are selected.',
									'woocommerce-bookings-availability'
								)
							}
						</Placeholder>
					) }
				</div>
			) }
		</Fragment>
	);
};

ProductsScreen.propTypes = {
	/**
	 * Callback to update the selected products.
	 */
	header: PropTypes.object,

	blockType: PropTypes.string,

	dateRangeType: PropTypes.string,

	infiniteScrollEnabled: PropTypes.bool,

	slotsPerPage: PropTypes.number,

	attributes: PropTypes.object,
};

export default ProductsScreen;
